body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #222020;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.myHeader {
  background-color: aqua;
}

.container, form{
  
  font-size: 16px;


  
}
header {
  height: 100px;
  
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0px;
  color: #fff;
}

.card {
  background-color: #fff;
  color: #333;
  border-radius: 15px;
  padding: 40px 50px;
  margin: 20px 0;
  position: relative;
}

.card.reverse {
  background-color: rgba(0,0,0,0,4);
  color: #fff;
}
.card h2 {
  font-size: 22px;
  font-weight: 600;
  text-align: center;
}

.close {
  position: absolute;
  top: 10px;
  right: 30px;
  cursor: pointer;
  background-color: none;
  border: none;
}

.edit {
  position: absolute;
  top: 10px;
  right: 0px;
  cursor: pointer;
  background-color: none;
  border: none;
}


.feedback-stats {
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.rating {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin: 30px 0 40px;
  cursor: pointer;
}

.input-group input {
  width: 80%;
}

[type='radio'] {
  opacity: 0;
}

[type = 'radio' ]:checked ~ label {
  background: #ff6a95;
  color: #fff;
  list-style: none;
}
ui, li{
  list-style: none;
}

.rating li:hover, .num-display {
    background-color: #ff6a95;
    color: #fff;
}